    let priority = "Urgent";
    sessionStorage.setItem('globalList', priority);
    this.getWorkTasks(priority);
    this.getOtherTasks(priority);
    this.getEducationTasks(priority);
    this.getHobbyTasks(priority);
    this.getSportTasks(priority);
